@import "variables";

body{
  font-size: 16px;
  line-height: 18px;
  display: grid;
  .header{
    display: flex;
    justify-content: space-around;
    height: 70px;
    background-color: #232323;
    border-bottom: 6px solid #440000;
    &__logo{
      a{
        display: block;
        font: 3em/70px @KaushanScript;
        color: #ff0000;
        text-decoration: none;
        span{
          font-size: .75em;
          color: #fff;
        }
      }
    }
  }
}