@import "components/variables";
@import "components/mixin";
@import "components/header";
@import "components/footer";

body{
  .content{
    display: grid;
    grid-template-columns: 5% 1fr 5%;
    grid-template-areas: ". container .";
    justify-items: center;
    perspective: 2000px;
    background-color: #121212;
    .container{
      grid-area: container;
      display: flex;
      width: 60%;
      height: 100vh;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      transition: 1s;
      transform: rotateX(55deg) rotateZ(-45deg) rotateY(10deg);
      transform-style: preserve-3d;
      .card{
        margin: 10px 20px;
        width: 300px;
        height: 400px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #24004e;
        transition: .3s;
        h2{
          font: 3em/3.5em @KaushanScript;
          color: #ff0000;
          span{
            color: #fff;
          }
        }
        &:hover{
          cursor: pointer;
          transform: translateZ(20px);
        }
      }
    }
    .container-origin{
      transform: rotate(0);
    }

  }
}